import React from "react";
import styled, { ThemeProvider } from "styled-components";
import { lightTheme } from "./Themes";
import { Design, Develope } from "./AllSvgs";
import LogoComponent from "../subComponents/LogoComponent";
import SocialIcon from "../subComponents/SocialIcons";
import PowerButton from "../subComponents/PowerButton";
import ParticalComponent from "../subComponents/ParticalComponent";
import BigTitle from "../subComponents/BigTitle";

const Box = styled.div`
  background-color: ${(props) => props.theme.body};
  width: 100vw;
  height: 100vh;
  position: relative;
  display:flex;
  justify-content: space-evenly;
  align-items:center;
`;

const Main = styled.div`
  border: 2px solid ${(props) => props.theme.text};
  border-radius:50px 0;
  color: ${(props) => props.theme.text};
  background-color: ${(props) => props.theme.body};
  width:30vw;
  height:60vh;
  padding:2rem;
  z-index: 3;
  line-height: 1.5;
  cursor:pointer;

  font-family: 'Ubuntu Mono',monospace;
  display: flex;
  flex-direction : column;
  justify-content: space-between; 

  &:hover{
    background-color:${(props) => props.theme.text};
    color:${(props) => props.theme.body};
  }
`
const Title = styled.h2`
display: flex;
justify-content: center;
align-items: center; 
font-size: calc(1rem + 1vw);

${Main}:hover &{
  &>*{
    fill:${(props) => props.theme.body};
  }
}
&>*:first-child{
  margin-right:1rem;
}
`

const Description = styled.div`
  color: ${(props) => props.theme.text};
  font-size: calc(0.6rem + 1vw);
  padding: 0.5rem 0;

  ${Main}:hover &{
      color:${(props) => props.theme.body};
  }
  
  strong{
    margin-bottom:1rem;
    text-transform:uppercase;
  }
  ul,p{
    margin-left:1.5rem;
  }

  ${Main}:hover{
    background-color:${(props) => props.theme.text};
    color:${(props) => props.theme.body};
  }
`

function MySkillPage() {
  return (
    <ThemeProvider theme={lightTheme}>
      <Box>
        <LogoComponent theme='light' />
        <SocialIcon theme='light' />
        <PowerButton />
        <ParticalComponent theme='light' />
        <Main>
          <Title>
            
              <Design width={40} height={40} /> Designer
            
          </Title>
          <Description>
              I love to create design which speaks, Keep it clean, minimal and
              simple.
            </Description>
            <Description>
              <strong>I like to Design</strong>
              <ul>
                <li>Web Design</li>
                <li>Mobil App</li>
              </ul>
            </Description>
            <Description>
              <strong>TOOLS</strong>
              <ul>
                <li>Figma</li>
              </ul>
            </Description>
        </Main>
        <Main>
          <Title>
            
              <Develope width={40} height={40} /> Frontend Developer{" "}
            
          </Title>
          <Description>
              I value business or brand for which i'm creating, thus i enjoy
              bringing new ideas to life.
            </Description>
            <Description>
              <strong>SKILLS</strong>
              <p>
                Html, Css, Js, React, Bootstrap, Firebase
                etc.
              </p>
            </Description>
            <Description>
              <strong>TOOLS</strong>
              <p>VScode, Github, Codepen etc.</p>
            </Description>
        </Main>
        <BigTitle text='SKILLS' top='80%' right='30%' />
      </Box>
    </ThemeProvider>
  );
}
export default MySkillPage;
